@charset "UTF-8";
@import url("./font/font.css");
/* useful color */
/* 리셋 */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

a {
  text-decoration: none;
  color: initial; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button {
  border: 0;
  cursor: pointer; }

button:focus {
  outline: none; }

/* 
	App's style
	html, body(+#root, .App), nav
*/
html, body, #root, .App {
  width: 100%;
  height: 100%;
  word-break: keep-all;
  font-family: "Manrope", sans-serif;
  overflow: hidden; }

body {
  background: linear-gradient(135deg, #7c1cff 0%, #b027f9 100%);
  background-size: 100%;
  background-attachment: fixed; }

nav {
  padding: 10vh 0 0 10vh;
  position: fixed;
  width: 100%;
  z-index: 10;
  transition: color 1s cubic-bezier(0.9, 0, 0.33, 0.94);
  color: white;
  font-size: 15px; }
  nav.color {
    color: #7c1cff; }
    nav.color .logo {
      background: #7c1cff; }
  nav .logo {
    position: relative;
    display: inline-block;
    width: 50px;
    height: 50px;
    background: #fff;
    color: transparent;
    transition: background-color 1s cubic-bezier(0.9, 0, 0.33, 0.94); }
  nav ul {
    display: flex;
    align-items: center;
    float: left; }
    nav ul li {
      cursor: pointer;
      float: left;
      margin-right: 30px; }
      nav ul li:not(:last-child) {
        margin-right: 30px; }
